
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Box from 'vux/src/components/box/index.vue'
import cookie from 'vux/src/tools/cookie/index.js'

import { isLoginWxAndLoginWx } from "../utils/login";

export default {
components: {
Group,
XInput,
XButton,
Cell,
Divider,
Box,
cookie,
},
data() {
return {
pid: sessionStorage.getItem("pid"),
mobile: "",
code: "",
verifyCode: "",
sms_code: "",
getVerifyUrl: "/api/gdekhback/special/get_verify.html",
getSmsUrl: "/api/gdekhback/special/get_sms.html",
loginUrl: "/api/gdekhback/special/login.html",
minutesCount: 0,
};
},
mounted() {
this.$vux.loading.show({ text: "加载中..." });
this.getVerify();
if (cookie.get("captcha")) {
let timestamp = cookie.get("captcha");
let now = new Date().getTime();
this.countDown(Math.round((timestamp - now) / 1000 + 60));
}
},
methods: {
getVerify() {
this.$http.post(this.getVerifyUrl).then((res) => {
this.verifyCode = this.getVerifyUrl + "?" + Math.random();
this.$vux.loading.hide();
});
},
sendMsg() {
this.$http
.post(this.getSmsUrl, {
mobile: this.mobile,
code: this.code,
pid: sessionStorage.getItem("pid"),
})
.then((res) => {
if (res.data.status === 0) {
this.$vux.toast.show({ text: res.data.info });
this.smsCookie();
} else {
this.$vux.toast.show({ text: res.data.info, type: "warn" });
}
});
},
smsCookie() {
let now = new Date().getTime();
cookie.set("captcha", now);
this.countDown(60);
},
countDown(num) {
if (num <= 0) {
this.minutesCount = 0;
return;
}
this.minutesCount = num;
let self = this;
let resend = setInterval(function () {
self.minutesCount--;
if (self.minutesCount === 0) {
clearInterval(resend);
}
}, 1000);
},
login() {
this.$vux.loading.show({
text: "加载中...",
});
this.$http
.post(this.loginUrl, {
code: this.code,
mobile: this.mobile,
sms_code: this.sms_code,
pid: sessionStorage.getItem("pid"),
})
.then((res) => {
if (res.data.status === 0) {
isLoginWxAndLoginWx(
this.pid,
this.$route.query.path || "/",
this.$route.query
).then((res) => {
this.$router.push({
path: this.$route.query.path || "/",
query: {
...this.$route.query,
},
});
});
} else {
this.$vux.toast.show({ text: res.data.info, type: "warn" });
}
this.$vux.loading.hide();
});
},
},
};



